// ÈöèÊú∫Ëé∑ÂèñÁÉ≠ËØçÊñπÊ≥ï
export function getRandomHotWord () {
  return hotWords[Math.floor(Math.random() * hotWords.length)];
}
export function generateHotSentence () {
  const template = sentenceTemplates[Math.floor(Math.random() * sentenceTemplates.length)];
  return template.replace('${word}', getRandomHotWord());
}
